.wrapper{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    button{
        background-color: #008CBA;
        border: none;
        color: white;
        padding: 15px 32px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 12px;
        margin-right: 20px;
        margin-left: 40px;
        margin-top: 10px;
        border-radius: 3px;
    }
    button:hover{
        box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    }
    button:disabled{
        background-color: #e7e7e7; 
        color: black;
    }
    h3{
        margin-left: 20px;
    }
}