.wrapper{
    button{
        background-color: #008CBA;
        border: none;
        color: white;
        padding: 15px 32px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 12px;
        margin-left: 40px;
        margin-top: 10px;
        border-radius: 3px;
    }
    button:hover{
        box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    }
    button:disabled{
        background-color: #e7e7e7; 
        color: black;
    }
    .articles{
        cursor: pointer;
        p{
            color: white;
           padding: 0;
           margin: 0;
        }
    }
}

.form{
    width: 400px;
    height: 300px;
    margin: 50px auto;
}

.input{
 margin: 10px 0;
}

.buttonsArea{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    a{
        padding-top: 20px;
        padding-left: 10px;
    }
    div{
        margin-right:10px ;
    }
}



.errors{
    p{
    color: rgb(49, 172, 38);
    }
}